.Capa{
    width: 100%;
    height:100vh;
    padding: 0px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
video{
    width: inherit;
    height: inherit;
    object-fit: cover;
    z-index: -1;    
}
.Capa--info{
    position: absolute;
    top: 70vh;
    z-index: 5; 
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-animation: MoveUpDown .5s linear  1s infinite alternate;
            animation: MoveUpDown .5s linear  1s infinite alternate;
}

.Capa--info h3{
    width: 100%;
    z-index: 5;
    color: #fff;
    font-weight: bold;
    font-size: 30px;
    text-shadow: 0px 0px 12px rgba(0,0,0,0.63);
}
.Capa--info .Capa--seta--down{
    font-size:80px;
    -webkit-filter:invert(1);
            filter:invert(1);
    margin-top: -10px
}
@-webkit-keyframes MoveUpDown {
    from {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    to { 
        -webkit-transform: scale(1.0); 
                transform: scale(1.0);
    }
}
@keyframes MoveUpDown {
    from {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    to { 
        -webkit-transform: scale(1.0); 
                transform: scale(1.0);
    }
}
.ComoFunciona{
    width: 100%; 
    min-height: 30vh ;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    margin: 10px;
}
.ComoFunciona h1{ 
    margin-top: 50px;
}
.ComoFunciona .Card--container{   
    margin-top: 30px; 
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;   
}
.ComoFunciona .Card{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;    
    padding: 5px;
    cursor: pointer;
    width: 340px;
    -webkit-transform: scale(.95);
            transform: scale(.95);
    transition: .5s;
}
.ComoFunciona .Card:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 10px;
    transition: .5s;
}
.ComoFunciona h3{
    font-size:14pt;
}
.ComoFunciona h4{
    font-size:smaller;
    color:var(--cor1);
}
.ComoFunciona img{
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 2px;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    transition: .5s;
    border-radius: 2px solid;
}
.Card--text{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap; 
    padding: 20px;
  
}
.Card--text h3{
    font-weight: 200;
    text-align: center;
    width:100%;
    color:var(--cor1);
}
