.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 9999;
}
.Header--logo{
    height: 40px;
    margin: 5px;
    cursor: pointer;
    display:inline-flex;
    justify-content: center;
    align-items: center;
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--logo h4{
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-left: 5px;
    width: 50px;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center; 
}
.Header--menu button{
    background-color: #14141400;
    margin: 0px 5px;
    padding: 0px 5px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size:18px;
}
.Header--contact{
    height: 25px;
    margin: 0px 5px;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;    
}
.Header--contact img{
    width: auto;
    height: 100%;
}
@media (max-width: 500px){ 
    .Header--logo h4{
        opacity: 0;
        margin-right: -40px;
    }
    .Header--menu button{
        font-size:16px;
    }
}

.Footer{
    height: 100%;
    width: 100%;
    padding: 20px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: rgba(128, 128, 128, 0.193);
}


.Protifolio{
    width: 100%;
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: -20px;
}
.Protifolio .card--conteriner{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card
{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70vw;
    border-radius: 2px;
    padding: 5px;
    
}
.Protifolio .card iframe,
.Protifolio .card img
{    
    width: calc(100% - 4px);
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
    border: 2px solid var(--cor2);
    padding-top:28px;
}
.Protifolio .card iframe{height:39.1vw;}
.Protifolio .card img{height: 40vw;}
.Protifolio .card--seta--left, .Protifolio .card--seta--right{
    margin-top: -100px;
    margin-right: 5px;
    margin-left: 5px;
    width:30px;
    height:30px;
    background-color: rgba(255, 255, 255, 0.624);
    -webkit-filter:invert(1);
            filter:invert(1);
    border-radius: 100%;
    z-index: 99;
    padding: 5px;   
    border: 2px solid black;
    opacity: 1;
    transition: 0.2s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    cursor: pointer;
}
.Protifolio .card--seta--left{
    left:15.5%;
}
.Protifolio .card--seta--right{    
    right:15.5%;
}

.Protifolio .card--seta--left:hover, .Protifolio .card--seta--right:hover{    
    transition: 0.2s;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.Protifolio h2{
    text-transform: uppercase;
    background-color: var(--cor2);
    width: 100%;
    margin: 2px;
    margin-bottom: -40px;
    z-index: 999;
    text-align: center;
    border-radius: 1px;
    color: white;
    
}
@media (max-width: 700px) {
    .Protifolio .card iframe{
        height: 55vw;
    }
    .Protifolio .card img{
        height: 80vw;
    }
    .Protifolio .card .iframe360{
        height: 90vw;
    }    
    .Protifolio .card
    {       
        width: 100vw;
    }  
    
    .Protifolio .card--seta--left{
        left:15px;
    }
    .Protifolio .card--seta--right{    
        right:15px;
    }    
}
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;    
}

.Preco{ 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    
}
.Preco .card--preco{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;    
    border-radius: 10px;
    margin-top: 30px;
}
.Preco .card--conteiner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    
}
.Preco .card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;    
    min-height: 310px; 
    box-shadow: 0px 0px 20px -1px rgba(0,0,0,0.65);    
    border-radius: 3px;
    height: 375px;
    width: 320px;
    padding: 10px;
    margin: 10px;
}
.Preco .card ol{
    width: 100%; 
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 0px;
    margin: auto;
}

.Preco .card ol li{
    width: 100%; 
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: nowrap;
    margin: 1px 5px;
}
.Preco .card ol img{
    width: 20px; 
    padding: 0px 5px;
}

.Preco h1{
    font-size: 30px;
    padding: 5px;
}

.Preco h2{
    font-size: 22px;
    font-weight: bold;
    margin: 10px 0px;
}
.Preco h3{
    font-size: 16px;
    font-weight: 100;
    padding: 5px;
}
.Preco h4{
    font-size: 16px;
    font-weight: 100;
}
.Preco h5{
    font-size: 12px;
    font-weight: 100;
    padding: 10px;
}
.DadosUser{
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 0 10px 15px 0 rgba(0, 0, 0 , .4);
    border-radius: 3px;
    width: 320px;
    padding: 10px;
    margin: 10px auto;
}
.DadosUser h1{
    font-size: 25px;
    padding-bottom: 15px;
    text-align: center;
}
.Dados .title{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;    
}
.title--item{
    padding: 3px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    color:var(--cor1)
}
.title--item h4{
    padding: 0px 15px;
}
.Dados{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 5px;
}
.Dados .card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;   
}
.Dados .item{  
    padding: 4px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
}
.Dados .item--input{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
}
.Dados input , .Dados select{
    padding: 2px;
    margin: 0px;
    text-align: center;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0 , .1);
    border: 0;
    outline: 0;
    width: 80px;
    background-color: white;
}
.Dados select{
cursor: pointer;
}
.DadosUser button {
    padding: 4px;
    margin: 10px 0px 0px 0px;
    text-align: center;
    cursor: pointer;
    outline: 0;
    border: 0;
    width: 100%;
    font-size: 18pt;
    background-color: var(--cor1);
    color: rgb(255, 255, 255);
    transition: .5s;
    
}
.DadosUser button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: .5s;
    background-color: rgba(6, 91, 250, 0.727);
}
