

.Protifolio{
    width: 100%;
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: -20px;
}
.Protifolio .card--conteriner{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card
{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70vw;
    border-radius: 2px;
    padding: 5px;
    
}
.Protifolio .card iframe,
.Protifolio .card img
{    
    width: calc(100% - 4px);
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
    border: 2px solid var(--cor2);
    padding-top:28px;
}
.Protifolio .card iframe{height:39.1vw;}
.Protifolio .card img{height: 40vw;}
.Protifolio .card--seta--left, .Protifolio .card--seta--right{
    margin-top: -100px;
    margin-right: 5px;
    margin-left: 5px;
    width:30px;
    height:30px;
    background-color: rgba(255, 255, 255, 0.624);
    filter:invert(1);
    border-radius: 100%;
    z-index: 99;
    padding: 5px;   
    border: 2px solid black;
    opacity: 1;
    transition: 0.2s;
    transform: scale(.9);
    cursor: pointer;
}
.Protifolio .card--seta--left{
    left:15.5%;
}
.Protifolio .card--seta--right{    
    right:15.5%;
}

.Protifolio .card--seta--left:hover, .Protifolio .card--seta--right:hover{    
    transition: 0.2s;
    transform: scale(1);
}

.Protifolio h2{
    text-transform: uppercase;
    background-color: var(--cor2);
    width: 100%;
    margin: 2px;
    margin-bottom: -40px;
    z-index: 999;
    text-align: center;
    border-radius: 1px;
    color: white;
    
}
@media (max-width: 700px) {
    .Protifolio .card iframe{
        height: 55vw;
    }
    .Protifolio .card img{
        height: 80vw;
    }
    .Protifolio .card .iframe360{
        height: 90vw;
    }    
    .Protifolio .card
    {       
        width: 100vw;
    }  
    
    .Protifolio .card--seta--left{
        left:15px;
    }
    .Protifolio .card--seta--right{    
        right:15px;
    }    
}